import { useState, useEffect } from "react";
import { sendMessage } from "src/services/extension"

export default function Home() {
   const [url, setUrl] = useState<string>("")

   useEffect(()=>{
       sendMessage("get_url", (url: string) => {
          setUrl(url)
       })
   },[])

  return (
    <div>Home</div>
  )
}
